.card {
  margin: 1.7vw 0.7vw;
  font-size: 2.2em;
  cursor: pointer;
  border: 1px red solid;
  border-radius: 5%;
  padding: 2.2vw 0.5vw;
  min-width: 1.4em;
  text-align: center;
  background-color: rgba(253, 59, 59, 0.055);
}

.flipped {
  animation: flip 200ms linear;
}

@keyframes flip {
  0% {
    opacity: 0;
    transform: rotateX(0);
  }
  100% {
    opacity: 1;
    transform: rotateX(90deg);
  }
}
